import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-vintage',
  templateUrl: './vintage.component.html',
  styleUrls: ['./vintage.component.css']
})
export class VintageComponent implements OnInit {

  VintageImage:Object[];
  
  constructor (private httpService: HttpClient) {

    this.httpService.get('./assets/json_file/vintage.json').subscribe(
      data => {
        this.VintageImage = data as string [];	 // FILL THE ARRAY WITH DATA.
        //  console.log(this.arrBirds[1]);
      },
      (err: HttpErrorResponse) => {
        console.log ("Image Not Found.....!");
      }
    );
   }

   ngOnInit() {
  }

}
